<div id="awards"><div class="awards__kv"><div class="awards__titleGroup"><div class="js-typed"><h2 class="awards__title has-cursor">區區修煉已經無法滿足了嗎？</h2></div><div class="js-typed"><h3 class="awards__subTitle has-cursor">還有比賽等著你！</h3></div></div><div class="awards__layers"><img class="awards__layer awards__layer-1" src="@/assets/images/s6-awards-layer-1.png"><img class="awards__layer awards__layer-2" src="@/assets/images/s6-awards-layer-2.png"><img class="awards__layer awards__layer-3" src="@/assets/images/s6-awards-layer-3.png"><img class="awards__car awards__car-1" src="@/assets/images/car-1.png"><img class="awards__car awards__car-2" src="@/assets/images/car-2.png"><img class="awards__car awards__car-3" src="@/assets/images/car-3.png"><img class="awards__car awards__car-4" src="@/assets/images/car-4.png"><img class="awards__car awards__car-5" src="@/assets/images/car-5.png"><div class="awards__car awards__car-main"><img class="awards__car awards__car-gif" src="@/assets/images/car-right.gif"><img class="awards__car awards__car-img" src="@/assets/images/car-main.png"></div><img class="awards__layer awards__layer-4" src="@/assets/images/s6-awards-layer-4.png"></div></div><div class="awards__content awards__content-1"><h4 class="js-awards-fadeText">評審機制</h4><h6 class="js-awards-fadeText">初選：</h6><p class="js-awards-fadeText">將由六角學院前端、UI 評審進行第一波篩選，</p><p class="js-awards-fadeText">並於 12/5（五）公布初選佳作名單！</p><h6 class="js-awards-fadeText">決選：</h6><p class="js-awards-fadeText">由三大企業針對該企業主題進行入圍獎最後篩選，</p><p class="js-awards-fadeText">並於 12/23（五）公布企業得獎名單！</p></div><div class="awards__content awards__content-2"><h4 class="js-awards-fadeText">獎項</h4><ul><li><div><h5 class="js-awards-fadeText">初選佳作 共六十位</h5><h5 class="is-heightLight js-awards-fadeText">數位獎狀</h5></div><p class="js-awards-fadeText">每週主題個人組十位、團體組十組</p></li><li><div><h5 class="js-awards-fadeText">個人企業獎 共六位</h5><h5 class="is-heightLight js-awards-fadeText">NTD 3,000/位</h5></div><p class="js-awards-fadeText">每週主題各兩名，設計一位、前端一位</p></li><li><h5 class="js-awards-fadeText">團體企業獎 共三組</h5><h5 class="is-heightLight js-awards-fadeText">NTD 10,000/組</h5><p class="js-awards-fadeText">每週主題各一組</p></li></ul></div><div class="awards__btnGroup"><a class="awards__cta" href="https://2022.thef2e.com" target="_blank" id="awards-cta"><div class="btn__overlay"></div><div class="btn__text">立即註冊報名！</div></a></div></div>